<template>
  <div id="new-form">
    <div class="container">
      <ul>
        <li>
          <span class="main-color">My Creatives</span>
          <router-link to="/" class="btn blue add-button">
            Back
          </router-link>
        </li>
        <hr />
      </ul>
      <div class="form">
        <form @submit.prevent="saveCreative" class="col s12">
          <h5 class="center-align main-color">New Creative</h5>
          <div class="row">
            <div class="col m4 left-align">
              <span>Creative ID</span>
            </div>
            <div class="col m6">
                <input placeholder="Write ID here" type="text" v-model="creative_id" required />
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Creative Name</span>
            </div>
            <div class="col m6">
                <input placeholder="Write name here" type="text" v-model="name" required />
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Device</span>
            </div>
            <div class="col m6">
              <input type="radio" id="mobile" value="Mobile" v-model="device" />
              <label class="btn grey" for="mobile">Mobile</label>
              <input type="radio" id="desktop" value="Desktop" v-model="device" />
              <label class="btn grey" for="desktop">Desktop</label><br />
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Name</span>
            </div>
            <div class="col m8 size">
                <input placeholder="Width" type="text" v-model="width" required />
                <input placeholder="Heigth" type="text" v-model="height" required />
            </div>
          </div>
          <hr />
          <div class="row">
            <div class="buttons col m10">
              <router-link to="/" class="main-color">Cancel</router-link>
              <button type="submit" class="btn blue">Create</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-creative',
  data () {
    return {
      creative_id: null,
      name: null,
      device: [],
      width: null,
      height: null
    }
  },
  methods: {
    saveCreative () {
      db.collection('creatives').add({
        creative_id: this.creative_id,
        name: this.name,
        device: this.device,
        width: this.width,
        height: this.height
      })
      .then(docRef => this.$router.push('/')
      )
      .catch(error => console.log(error))
    }
  }
}
</script>



