<template>
  <div id="view-form">
    <div class="container">
    <ul>
      <li>
        <h5 class="title">Form information</h5>
      </li>
      <hr />
    </ul>
    <ul class="">
      <li class="collection-header">
        <span class="main-color">Form ID:</span> <span>{{creative_id}}</span>
      </li>
      <li class="collection-item">
        <span class="main-color">Creative name:</span> <span>{{name}}</span>
      </li>
      <li class="collection-item">
        <span class="main-color">Device:</span> <span>{{device}}</span>
      </li>
      <li class="collection-item">
        <span class="main-color">Placement size:</span><span>{{width}} x {{height}}</span>
      </li>
      <hr />
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteCreative" class="btn red darken-4 delete">Delete</button>
    <router-link v-bind:to="{name: 'edit-form', params: {creative_id: creative_id}}" class="btn blue">
      Edit
    </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-form',
  data () {
    return {
      creative_id: null,
      name: null,
      device: null,
      width: null,
      height: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('creatives').where('creative_id', '==', to.params.creative_id).get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.creative_id = doc.data().creative_id
          vm.name = doc.data().name
          vm.device = doc.data().device
          vm.width = doc.data().width
          vm.height = doc.data().height
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('creatives').where('creative_id', '==', this.$route.params.creative_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.creative_id = doc.data()
          .creative_id
          this.name = doc.data().name
          this.device = doc.data().device
          this.width = doc.data().width
          this.heigth = doc.data().height
        })
      })
    },
    deleteCreative () {
      if(confirm('Are you sure')) {
        db.collection('creatives').where('creative_id', '==', this.$route.params.creative_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete();
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>



