<template>
  <div id="dashboard">
    <div class="container">
      <ul>
        <li>
          <span class="main-color">My Creatives</span>
          <router-link to="/new" class="btn blue add-button">
            Add
          </router-link>
        </li>
        <hr />
      </ul>
        <table>
          <thead>
            <tr>
            <th>
              Name
            </th>
            <th>
              Device
            </th>
            <th>
              Size
            </th>
            <th>
              Action
            </th>
            </tr>
          </thead>
          <tbody v-for="creative in creatives" class="collection-item">
            <tr>
              <td>
                {{creative.name}}
              </td>
              <td>
                {{creative.device}}
              </td>
              <td>
                {{creative.width}} x {{creative.height}}
              </td>
              <td>
                <router-link v-bind:to="{name: 'view-form', params: {creative_id: creative.creative_id}}">Modify</i></router-link>
              </td>
            </tr>
          </tbody>
        </table>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      creatives: [],
    }
  },
  created () {
    db.collection('creatives').orderBy('name').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'creative_id': doc.data().creative_id,
          'name': doc.data().name,
          'device': doc.data().device,
          'width': doc.data().width,
          'height': doc.data().height
        }
        this.creatives.push(data)
      })
    })
  }
}
</script>



