<template>
  <div id="edit-form">
    <ul>
      <li>
        <span class="main-color">Edit form</span>
        <router-link to="/" class="btn blue add-button">
          Back
        </router-link>
      </li>
      <hr />
    </ul>
    <div class="form">
        <form @submit.prevent="updateCreative" class="col s12">
          <h5 class="center-align main-color">Edit Creative</h5>
          <div class="row hide">
            <div class="col m4 left-align">
              <span>Creative ID</span>
            </div>
            <div class="col m6">
                <input type="text" v-model="creative_id" disabled/>
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Creative Name</span>
            </div>
            <div class="col m6">
                <input placeholder="Write name here" type="text" v-model="name" required />
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Device</span>
            </div>
            <div class="col m6">
              <input type="radio" id="mobile" value="Mobile" v-model="device" />
              <label class="btn grey" for="mobile">Mobile</label>
              <input type="radio" id="desktop" value="Desktop" v-model="device" />
              <label class="btn grey" for="desktop">Desktop</label><br />
            </div>
          </div>
          <div class="row">
            <div class="col m4 left-align">
              <span>Name</span>
            </div>
            <div class="col m8 size">
                <input placeholder="Width" type="text" v-model="width" required />
                <input placeholder="Heigth" type="text" v-model="height" required />
            </div>
          </div>
          <hr />
          <div class="row">
            <div class="buttons col m10">
              <router-link to="/" class="main-color">Cancel</router-link>
              <button type="submit" class="btn blue">Save</button>
            </div>
          </div>
        </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-form',
  data () {
    return {
      creative_id: null,
      name: null,
      device: [],
      width: null,
      height: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('creatives').where('creative_id', '==', to.params.creative_id).get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.creative_id = doc.data().creative_id
          vm.name = doc.data().name
          vm.device = doc.data().device
          vm.width = doc.data().width
          vm.height = doc.data().height
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('creatives').where('creative_id', '==', this.$route.params.creative_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.creative_id = doc.data()
          .creative_id
          this.name = doc.data().name
          this.device = doc.data().device
          this.width = doc.data().width
          this.heigth = doc.data().height
        })
      })
    },
    updateCreative () {
      db.collection('creatives').where('creative_id', '==', this.$route.params.creative_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            creative_id: this.creative_id,
            name: this.name,
            device: this.device,
            width: this.width,
            height: this.height
          })
          .then(() => {
            this.$router.push({name: 'view-form',
            params: {creative_id: this.creative_id}})
          })
        })
      })
    }
  }
}
</script>



